.hero {
  position: relative;
  width: 100%;
  background-image: url(/build/img/bg-hero.webp);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.contenido-hero {
  width: min(100%, 1500px);
  min-height: 30rem;
  display: grid;
  place-items: center;
  color: #F2F2F2;
  text-align: center;
  gap: 1.6rem;
}

.manchaMorada {
  background-image: url(../../../src/img/manchaMorada.webp);
  background-position: center 60%;
  background-size: contain;
  background-repeat: no-repeat;
  white-space: nowrap;
}

.subcontenido-hero {
  width: min(80%, 1300px);
}

.titulo-hero {
  font-size: clamp(1.4rem, 7vw, 10rem);
  line-height: 1.2;
  margin: 0;
}

.subtitulo-hero {
  font-size: clamp(1.7rem, 2.5vw, 4rem);
  line-height: 1.3;
  margin: 0;
}

.boton-wsp {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  background-color: #FEBD1B;
  color: #F2F2F2;
  padding: 1.8rem 8rem;
  font-family: "fonnts_comAcumin_Pro_SemiCondensed";
  font-size: 3.2rem;
  font-weight: bold;
  text-decoration: none;
  cursor: pointer;
  transition: background 0.3s;
  border-bottom: 1rem solid #FE821C;
}
.boton-wsp img.icono-wsp {
  width: 3.4rem;
  height: 3.4rem;
}

.boton-wsp:hover {
  background-color: rgb(228.9956331878, 163.711790393, 1.0043668122);
}

@media (max-width: 767.98px) {
  .boton-wsp {
    width: 100%;
    max-width: 28rem;
    margin: 1rem auto;
    padding: 1.2rem 2rem;
    font-size: clamp(1.6rem, 5vw, 2rem);
    border-bottom: 0.6rem solid #FE821C;
  }
  .boton-wsp img.icono-wsp {
    width: 2.4rem;
    height: 2.4rem;
  }
}
@media (min-width: 768px) {
  .hero {
    height: auto;
    padding: 4rem;
  }
  .contenido-hero {
    gap: 3rem;
  }
}
@media (min-width: 120rem) {
  .hero {
    min-height: 100rem;
  }
}
footer {
  background: #4A85C5;
  background: linear-gradient(90deg, rgb(74, 133, 197) 0%, rgb(87, 71, 149) 100%);
  display: flex;
  top: 0;
  z-index: 1000;
  margin: 0;
  width: 100%;
}

.footer-nav {
  justify-content: space-between;
  height: 22rem;
  background: #4A85C5;
  background: linear-gradient(90deg, rgb(74, 133, 197) 0%, rgb(87, 71, 149) 100%);
  display: flex;
  top: 0;
  z-index: 1000;
  margin: 0;
  width: auto;
  gap: 2rem;
}

.footer-nav a .logo {
  width: 48.9rem;
  display: block;
}

footer a {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  padding: 1.5rem 0;
  text-align: center;
  font-size: 2rem;
}

.enlace-footer {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.enlace-logo-footer {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.logo-footer {
  width: 70%;
  padding: 3rem 0 0 0;
}

.icono-footer {
  width: 70%;
}

.navegacion-principal a:hover {
  background-color: #E27415;
}

@media (max-width: 767.98px) {
  .footer {
    display: grid;
    place-items: center;
    padding: 0.8rem 1rem;
  }
  .footer-nav {
    display: grid;
    grid-template-columns: repeat(4, auto);
    justify-content: center;
    align-items: center;
    row-gap: 0.8rem;
    column-gap: 1.2rem;
    height: auto;
    width: 100%;
  }
  .footer-nav .enlace-logo-footer {
    grid-column: 1/-1;
    justify-self: center;
    align-self: center;
    padding: 0.2rem;
  }
  .footer-nav .enlace-footer {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .logo-footer {
    width: clamp(10rem, 40vw, 15rem);
    height: auto;
    display: block;
    padding: 0rem;
  }
  .icono-footer {
    width: 25%;
    display: block;
  }
}
